* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #1C1C1C;
    height: 100vh;
    width: 100vw;
}

#root {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}

.app {
    display: flex;
    width: 90vw;
    max-width: 1000px;
}

.intro-page{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: inherit;
    height: inherit;
    background-image: url('./images/intro-page.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;   
}

.intro-title {
    font-family: 'Karla', sans-serif;
    font-weight: 700;
    font-size: 35px;
    color: #293264;
    margin: 0;
}

.intro-subtitle {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;
    color: #293264;
    margin: 0;
    margin-top: 10px;
}

.intro-button {
    width: 190px;
    height: 52px;
    border-radius: 15px;
    background-color: #4D5B9E;
    color: #F5F7FB;
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 16px;
    border: none;
    cursor: pointer;
    margin-top: 30px;
}

.intro-button:hover {
    background-color: #434f89;
}

.quiz {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: inherit;
    height: inherit;
    padding: 20px;
    background-image: url('./images/quiz-page.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.question {
    font-family: 'Karla', sans-serif;
    font-weight: 700;
    font-size: 16px;
    color: #293264;
}

.answers {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 15px;
}

.answer-button {
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 13px;
    color: #293264;
    border-radius: 8px;
    border: 1px solid #4D5B9E;
    padding: 3px 7px;
    min-width: 60px;
}

.answer-button-selected {
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 13px;
    color: #293264;
    background-color: #D6DBF5;
    border-radius: 8px;
    border: none;
    padding: 3px 7px;
    min-width: 60px;
}

.answer-button-checked {
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 13px;
    color: #293264;
    border-radius: 8px;
    border: 1px solid #4D5B9E;
    padding: 3px 7px;
    min-width: 60px;
    opacity: 0.5;
}

.answer-button-incorrect {
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 13px;
    color: #293264;
    background-color: #F8BCBC;
    border-radius: 8px;
    border: none;
    padding: 3px 7px;
    min-width: 60px;
    opacity: 0.5; 
}

.answer-button-correct {
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 13px;
    color: #293264;
    background-color: #94D7A2;
    border-radius: 8px;
    border: none;
    padding: 3px 7px;
    min-width: 60px;
}

.correct-answer {
    background-color: green;
}

hr {
    border: none;
    border-top: 1px solid #DBDEF0;
    margin-top: 15px;
    margin-bottom: 16px;
}

.check-button {
    padding: 10px 20px;
    border-radius: 15px;
    background-color: #4D5B9E;
    color: #F5F7FB;
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 13px;
    border: none;
    cursor: pointer;
}

.check-button:hover {
    background-color: #434f89;
}

.footer-check {
    display: flex;
    align-items: center;
    gap: 10px;
}

.counter {
    font-family: 'Karla', sans-serif;
    font-weight: 700;
    font-size: 15px;
    color: #293264;  
}